import React, { useState, useEffect } from 'react';
import { withRouter } from 'react-router-dom';
import clone from 'lodash/cloneDeep';
import {
  Breadcrumb,
  Card,
  Tabs,
  Row,
  Col,
  Table,
  Button,
  Tooltip,
  Popconfirm,
  message,
  Icon,
  Alert,
  Modal,
  Spin,
  Tag,
} from 'antd';
import styled from 'styled-components';
import { CategoryForm } from './categoryForm';
import { SubscriptionsForm } from './subscriptionsForm';
import {
  getCategories,
  delCategory,
  getSubscriptions,
  getUserSubscription,
  delSubscription,
  assignSubscription,
} from './../../services';
const { TabPane } = Tabs;

const ButtonAction = styled(Button)`
  margin: 5px;
`;

const ColManage = styled(Col)`
  display: flex;
  justify-content: center;
  align-items: center;
`;

const Subscriptions = props => {
  const { messages, history } = props;

  const [userSubscription, setUserSubscription] = useState(null);
  const [category, setCategory] = useState(null);
  const [subscription, setSubscription] = useState({
    canUsePGB: false,
    canTrain: false,
    canUseApi: false,
  });
  const [loading, setLoading] = useState(false);
  const [loadingUser, setLoadingUser] = useState(false);
  const [loadingCat, setLoadingCat] = useState(false);
  const [tab, setTab] = useState('subscription');
  const [visible, setVisible] = useState(false);
  const [visibleList, setVisibleList] = useState(true);
  const [visibleModal, setVisibleModal] = useState(false);
  const [categories, setCategories] = useState([]);
  const [subscriptions, setSubscriptions] = useState([]);

  useEffect(() => {
    if (props.match && props.match.params && props.match.params.id) {
      setVisibleList(false);
      fetchSubscription(props.match.params.id);
    } else if (props.userLogin.rol === 'client') {
      return (window.location = `/dashboard/subscriptions/user/${props.userLogin.id}`);
    }

    setLoading(true);
    fetchSubscriptions();
    fetchCategories();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const operations = (
    <Button
      loading={loading}
      onClick={() => {
        setVisible(true);
      }}
      type="primary"
      icon="plus"
    >
      {tab === 'subscription' ? messages.subscription.add : messages.subscription.addCat}
    </Button>
  );

  const changeTab = key => {
    setTab(key);
  };

  const hideModal = () => {
    setSubscription({
      canUsePGB: false,
      canTrain: false,
      canUseApi: false,
    });
    setCategory(null);
    setVisible(false);
  };

  const editCat = record => {
    setCategory(record);
    setVisible(true);
  };

  const editSusc = record => {
    setSubscription(record);
    setVisible(true);
  };

  const fetchCategories = async () => {
    try {
      const response = await getCategories();
      setCategories(response.data);
      setLoading(false);
    } catch (error) {
      setLoading(false);
    }
  };

  const removeCategory = async id => {
    try {
      setLoadingCat(id);
      await delCategory(id);
      await fetchCategories();
      setLoadingCat(false);
    } catch (error) {
      message.error(error, message);
      setLoadingCat(false);
    }
  };

  const fetchSubscriptions = async () => {
    try {
      const response = await getSubscriptions();
      setSubscriptions(response.data);
      setLoading(false);
    } catch (error) {
      setLoading(false);
    }
  };

  const fetchSubscription = async id => {
    try {
      setLoadingUser(true);
      const response = await getUserSubscription(id);
      if (response.data) setUserSubscription(response.data);
      setLoadingUser(false);
    } catch (error) {
      message.error(error.message);
      setLoadingUser(false);
      history.push('/dashboard/users');
    }
  };

  const assign = async record => {
    try {
      const form = {
        userId: props.match.params.id,
        subscriptionId: record.id,
      };
      setLoading(true);
      const response = await assignSubscription(form);
      setUserSubscription(response.data);
      setVisibleModal(false);
      message.success(messages.default.success);
      setLoading(false);
    } catch (error) {
      message.error(error.message);
      setLoading(false);
    }
  };

  const removeSubscription = async id => {
    try {
      setLoadingCat(id);
      await delSubscription(id);

      setLoadingCat(false);
    } catch (error) {
      message.error(error, message);
      setLoadingCat(false);
    }
  };

  const cat_columns = [
    {
      title: 'Name',
      dataIndex: 'name',
      key: 'name',
    },
    {
      key: 'action',
      width: 230,
      fixed: 'right',
      render: (text, record) => (
        <Row type="flex" justify="end">
          <Tooltip placement="top" title={messages.subscription.editCat}>
            <ButtonAction
              onClick={() => {
                editCat(record);
              }}
              size="small"
              type="primary"
              shape="circle"
              icon="edit"
            />
          </Tooltip>

          <Tooltip placement="top" title={messages.subscription.removeCat}>
            <Popconfirm
              onConfirm={() => {
                removeCategory(record.id);
              }}
              title={messages.subscription.confirmDeleteCat}
              okText={messages.default.yes}
              cancelText={messages.default.no}
            >
              <ButtonAction
                loading={parseInt(loadingCat) === parseInt(record.id)}
                size="small"
                type="danger"
                shape="circle"
                icon="delete"
              />
            </Popconfirm>
          </Tooltip>
        </Row>
      ),
    },
  ];

  const sus_columns = [
    {
      title: 'Title',
      dataIndex: 'title',
      key: 'title',
    },
    {
      title: 'Category',
      dataIndex: 'CategoryId',
      key: 'category',
      render: text => {
        const cat = categories.filter(item => {
          return parseInt(item.id) === parseInt(text);
        });
        if (cat && cat.length) return cat[0].name;
        return text;
      },
    },
    {
      title: 'Max Pages',
      dataIndex: 'pages',
      key: 'pages',
    },
    {
      title: 'Price Per Additional Page',
      dataIndex: 'pricePerAdditionalPage',
      key: 'pricePerAdditionalPage',
    },
    {
      title: 'Free Pages/Days',
      dataIndex: 'freePages',
      key: 'freePages',
      render: (text, record) => {
        return `${record.freePages}/${record.freeDays}`;
      },
    },
    {
      title: 'Price',
      dataIndex: 'price',
      key: 'price',
    },
    {
      title: 'Min Price',
      dataIndex: 'minPrice',
      key: 'minPrice',
    },
    {
      title: 'Period (Month)',
      dataIndex: 'period',
      key: 'period',
    },
    {
      title: 'MaxEngines Shared/Private',
      dataIndex: 'maxSharedEngines',
      key: 'maxSharedEngines',
      render: (text, record) => {
        return `${record.maxSharedEngines}/${record.maxPrivateInstances}`;
      },
    },
    {
      title: 'Max Users',
      dataIndex: 'maxUsers',
      key: 'maxUsers',
    },
    {
      title: 'Max Clones',
      dataIndex: 'maxClones',
      key: 'maxClones',
    },
    {
      title: 'CanUsePGB',
      dataIndex: 'canUsePGB',
      key: 'canUsePGB',
      render: text => {
        if (text) return <Icon type="check-circle" theme="twoTone" twoToneColor="#52c41a" />;
        return <Icon type="close-circle" theme="twoTone" twoToneColor="#f5222d" />;
      },
    },
    {
      title: 'Can Train',
      dataIndex: 'canTrain',
      key: 'canTrain',
      render: text => {
        if (text) return <Icon type="check-circle" theme="twoTone" twoToneColor="#52c41a" />;
        return <Icon type="close-circle" theme="twoTone" twoToneColor="#f5222d" />;
      },
    },
    {
      title: 'Can Use Api',
      dataIndex: 'canUseApi',
      key: 'canUseApi',
      render: text => {
        if (text) return <Icon type="check-circle" theme="twoTone" twoToneColor="#52c41a" />;
        return <Icon type="close-circle" theme="twoTone" twoToneColor="#f5222d" />;
      },
    },
    {
      title: messages.subscription.recommended,
      dataIndex: 'recommended',
      key: 'recommended',
      render: (text, record) => {
        if (text) return <Icon type="check-circle" theme="twoTone" twoToneColor="#52c41a" />;
        return <Icon type="close-circle" theme="twoTone" twoToneColor="#f5222d" />;
      },
    },
    {
      title: messages.subscription.active,
      dataIndex: 'active',
      key: 'active',
      render: (text, record) => {
        if (text) return <Icon type="check-circle" theme="twoTone" twoToneColor="#52c41a" />;
        return <Icon type="close-circle" theme="twoTone" twoToneColor="#f5222d" />;
      },
    },
    {
      key: 'action',
      width: 100,
      fixed: 'right',
      render: (text, record) => (
        <Row type="flex" justify="end">
          <Tooltip placement="top" title={messages.subscription.editCat}>
            <ButtonAction
              onClick={() => {
                editSusc(record);
              }}
              size="small"
              type="primary"
              shape="circle"
              icon="edit"
            />
          </Tooltip>
          <Tooltip placement="top" title={messages.subscription.removeCat}>
            <Popconfirm
              onConfirm={() => {
                removeSubscription(record.id);
              }}
              title={messages.subscription.confirmDeleteSus}
              okText={messages.default.yes}
              cancelText={messages.default.no}
            >
              <ButtonAction
                loading={parseInt(loadingCat) === parseInt(record.id)}
                size="small"
                type="danger"
                shape="circle"
                icon="delete"
              />
            </Popconfirm>
          </Tooltip>
        </Row>
      ),
    },
  ];

  let m_sus_columns = clone(sus_columns);
  m_sus_columns = m_sus_columns.splice(0, 14);
  m_sus_columns.push({
    key: 'action',
    width: 100,
    fixed: 'right',
    render: (text, record) => {
      if (userSubscription && record.id === userSubscription.id) {
        return (
          <Tag color="green">
            <Icon type="check-circle" theme="twoTone" twoToneColor="#52c41a" /> SELECTED
          </Tag>
        );
      }
      return (
        <Row type="flex" justify="end">
          <Popconfirm
            onConfirm={() => {
              assign(record);
            }}
            title={messages.subscription.confirmAssignSus}
            okText={messages.default.yes}
            cancelText={messages.default.no}
          >
            <ButtonAction loading={loading} size="small" type="primary" icon="select">
              Assign
            </ButtonAction>
          </Popconfirm>
        </Row>
      );
    },
  });

  let mm_sus_columns = clone(sus_columns);
  mm_sus_columns = mm_sus_columns.splice(0, 14);
  if (props.userLogin.rol === 'admin') {
    mm_sus_columns.push({
      key: 'action',
      width: 100,
      fixed: 'right',
      render: (text, record) => (
        <Row type="flex" justify="end">
          <Button
            onClick={() => {
              setVisibleModal(true);
            }}
            type="primary"
            icon="bell"
          >
            Manage User Subscriptions
          </Button>
        </Row>
      ),
    });
  }

  const handleCancel = () => {
    setVisibleModal(false);
  };

  return (
    <React.Fragment>
      <Card>
        {visibleList && (
          <Tabs defaultActiveKey={tab} tabBarExtraContent={operations} onChange={changeTab}>
            <TabPane tab={messages.menus.subscription} key="subscription">
              <Row>
                <Col xs={24}>
                  <Table
                    loading={loading}
                    size="small"
                    dataSource={subscriptions}
                    columns={sus_columns}
                    rowKey={record => record.id}
                  />
                </Col>
              </Row>
            </TabPane>
            <TabPane tab={messages.subscription.categories} key="categories">
              <Row>
                <Col xs={24}>
                  <Table
                    loading={loading}
                    size="small"
                    dataSource={categories}
                    columns={cat_columns}
                    rowKey={record => record.id}
                  />
                </Col>
              </Row>
            </TabPane>
          </Tabs>
        )}

        {!visibleList && (
          <React.Fragment>
            <Spin spinning={loadingUser} tip="Loading...">
              {userSubscription === null && (
                <Row type="flex" justify="center">
                  <Col xs={24} md={12}>
                    <Alert
                      message="Subscription Notes"
                      description="This user has no assigned subscription, to assign the subscription can be done through the following button."
                      type="info"
                      showIcon
                    />
                  </Col>
                  <ColManage xs={24} md={12}>
                    <Button
                      onClick={() => {
                        setVisibleModal(true);
                      }}
                      type="primary"
                      icon="bell"
                    >
                      Manage User Subscriptions
                    </Button>
                  </ColManage>
                </Row>
              )}
              {userSubscription !== null && (
                <Table
                  loading={loading}
                  size="small"
                  dataSource={[userSubscription]}
                  columns={mm_sus_columns}
                  rowKey={record => record.id}
                />
              )}
            </Spin>
            <Modal
              width={`90%`}
              title="Manage User Subscriptions"
              visible={visibleModal}
              onCancel={handleCancel}
            >
              <Table
                loading={loading}
                size="small"
                dataSource={subscriptions}
                columns={m_sus_columns}
                rowKey={record => record.id}
              />
            </Modal>
          </React.Fragment>
        )}

        <CategoryForm
          category={category}
          messages={messages}
          visible={visible && tab === 'categories'}
          hideModal={hideModal}
          fetchCategories={fetchCategories}
        />

        <SubscriptionsForm
          subscription={subscription}
          categories={categories}
          messages={messages}
          visible={visible && tab === 'subscription'}
          hideModal={hideModal}
          fetchSubscriptions={fetchSubscriptions}
        />
      </Card>
    </React.Fragment>
  );
};

export default withRouter(Subscriptions);
