import React, { useState } from 'react';
import { Form, Modal, Button, Row, Col, Input, message, InputNumber, Select, Switch } from 'antd';

import { addSubscription, saveSubscription } from './../../services';

const FormItem = Form.Item;
const { Option } = Select;

const SubscriptionsFormCmp = props => {
  const {
    messages,
    categories,
    visible,
    hideModal,
    subscription,
    fetchSubscriptions,
    form: { getFieldDecorator },
    form,
  } = props;

  const [loading, setLoading] = useState(false);

  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 16 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 8 },
    },
  };
  const formItemLayoutTitle = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 8 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 16 },
    },
  };

  const handleReset = () => {
    form.resetFields();
  };

  const submit = e => {
    e.preventDefault();

    form.validateFields(async (err, values) => {
      if (!err) {
        const form = {
          title: values.title,
          CategoryId: values.CategoryId,
          pages: values.pages,
          freePages: values.freePages,
          price: values.price,
          minPrice: values.minPrice,
          pricePerAdditionalPage: values.pricePerAdditionalPage,
          period: values.period,
          freeDays: values.freeDays,
          maxSharedEngines: values.maxSharedEngines,
          maxPrivateInstances: values.maxPrivateInstances,
          maxUsers: values.maxUsers,
          maxClones: values.maxClones,
          canUsePGB: values.canUsePGB || subscription.canUsePGB || false,
          canTrain: values.canTrain || subscription.canTrain || false,
          canUseApi: values.canUseApi || subscription.canUseApi || false,
          recommended: values.recommended || subscription.recommended || false,
          active: values.active || subscription.active || false,
        };

        try {
          setLoading(true);
          if (subscription && subscription.id) {
            form.id = subscription.id;
            await saveSubscription(form);
          } else {
            await addSubscription(form);
          }

          fetchSubscriptions();

          hideModal();
          setLoading(false);
          handleReset();
          message.success(messages.default.success);
        } catch (error) {
          setLoading(false);
          handleReset();
          message.error(error.message);
        }
      }
    });
  };

  const onChange = value => {};

  const onBlur = () => {};

  const onFocus = () => {};

  const onSearch = val => {};

  const hideModalCmp = val => {
    handleReset();
    hideModal();
  };

  return (
    <Modal
      centered
      width={600}
      title={
        subscription && subscription.id ? messages.subscription.edit : messages.subscription.add
      }
      visible={visible}
      onOk={hideModalCmp}
      onCancel={hideModalCmp}
      footer={[
        <Button key="back" onClick={hideModalCmp}>
          {messages.default.cancel}
        </Button>,
        <Button key="submit" type="primary" loading={loading} onClick={submit}>
          {subscription && subscription.id ? messages.default.save : messages.default.create}
        </Button>,
      ]}
    >
      <Form {...formItemLayout} onSubmit={submit} autocomplete="off">
        <Row justify="center" type="flex">
          <Col xs={24}>
            <FormItem {...formItemLayoutTitle} label={messages.subscription.title} hasFeedback>
              {getFieldDecorator('title', {
                initialValue: subscription && subscription.title ? subscription.title : '',
                rules: [
                  {
                    required: true,
                    message: messages.subscription.rules.title.required,
                  },
                ],
              })(<Input type="text" />)}
            </FormItem>
          </Col>
          <Col xs={24}>
            <FormItem {...formItemLayoutTitle} label={messages.subscription.category} hasFeedback>
              {getFieldDecorator('CategoryId', {
                initialValue:
                  subscription && subscription.CategoryId ? subscription.CategoryId : '',
                rules: [
                  {
                    required: true,
                    message: messages.subscription.rules.category.required,
                  },
                ],
              })(
                <Select
                  showSearch
                  placeholder="Select a category"
                  optionFilterProp="children"
                  onChange={onChange}
                  onFocus={onFocus}
                  onBlur={onBlur}
                  onSearch={onSearch}
                  filterOption={(input, option) =>
                    option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                  }
                >
                  {categories.map(item => {
                    return (
                      <Option key={item.id} value={item.id}>
                        {item.name}
                      </Option>
                    );
                  })}
                </Select>
              )}
            </FormItem>
          </Col>
          <Col xs={24}>
            <FormItem {...formItemLayoutTitle} label={messages.subscription.period} hasFeedback>
              {getFieldDecorator('period', {
                initialValue: 1,
                rules: [
                  {
                    required: true,
                    message: messages.subscription.rules.period.required,
                  },
                ],
              })(
                <Select
                  showSearch
                  placeholder="Select a period"
                  optionFilterProp="children"
                  onChange={onChange}
                  onFocus={onFocus}
                  onBlur={onBlur}
                  onSearch={onSearch}
                  filterOption={(input, option) =>
                    option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                  }
                >
                  <Option value={1}>Month</Option>
                  <Option value={3}>Quarter</Option>
                  <Option value={6}>6 Month</Option>
                  <Option value={12}>Annual</Option>
                  <Option value={24}>Biannual</Option>
                </Select>
              )}
            </FormItem>
          </Col>
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.pages} hasFeedback>
              {getFieldDecorator('pages', {
                initialValue: subscription && subscription.pages ? subscription.pages : '',
                rules: [
                  {
                    required: true,
                    message: messages.subscription.rules.pages.required,
                  },
                ],
              })(<Input type="text" />)}
            </FormItem>
          </Col>
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.freePages} hasFeedback>
              {getFieldDecorator('freePages', {
                initialValue: subscription && subscription.freePages ? subscription.freePages : '',
                rules: [
                  {
                    required: true,
                    message: messages.subscription.rules.freePages.required,
                  },
                ],
              })(<Input type="text" />)}
            </FormItem>
          </Col>
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.price} hasFeedback>
              {getFieldDecorator('price', {
                initialValue: subscription && subscription.price ? subscription.price : '',
                rules: [
                  {
                    required: true,
                    message: messages.subscription.rules.price.required,
                  },
                ],
              })(<InputNumber min={0} />)}
            </FormItem>
          </Col>
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.minPrice} hasFeedback>
              {getFieldDecorator('minPrice', {
                initialValue: subscription && subscription.minPrice ? subscription.minPrice : '',
                rules: [
                  {
                    required: true,
                    message: messages.subscription.rules.minPrice.required,
                  },
                ],
              })(<InputNumber min={0} />)}
            </FormItem>
          </Col>
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.pricePerAdditionalPage} hasFeedback>
              {getFieldDecorator('pricePerAdditionalPage', {
                initialValue:
                  subscription && subscription.pricePerAdditionalPage
                    ? subscription.pricePerAdditionalPage
                    : '',
                rules: [
                  {
                    required: true,
                    message: messages.subscription.rules.pricePerAdditionalPage.required,
                  },
                ],
              })(<InputNumber min={0} />)}
            </FormItem>
          </Col>
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.freeDays} hasFeedback>
              {getFieldDecorator('freeDays', {
                initialValue: subscription && subscription.freeDays ? subscription.freeDays : '',
                rules: [
                  {
                    required: true,
                    message: messages.subscription.rules.freeDays.required,
                  },
                ],
              })(<InputNumber min={0} />)}
            </FormItem>
          </Col>
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.maxSharedEngines} hasFeedback>
              {getFieldDecorator('maxSharedEngines', {
                initialValue:
                  subscription && subscription.maxSharedEngines
                    ? subscription.maxSharedEngines
                    : '',
                rules: [
                  {
                    required: true,
                    message: messages.subscription.rules.maxSharedEngines.required,
                  },
                ],
              })(<InputNumber min={0} />)}
            </FormItem>
          </Col>
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.maxPrivateInstances} hasFeedback>
              {getFieldDecorator('maxPrivateInstances', {
                initialValue:
                  subscription && subscription.maxPrivateInstances
                    ? subscription.maxPrivateInstances
                    : '',
                rules: [
                  {
                    required: true,
                    message: messages.subscription.rules.maxPrivateInstances.required,
                  },
                ],
              })(<InputNumber min={0} />)}
            </FormItem>
          </Col>
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.maxUsers} hasFeedback>
              {getFieldDecorator('maxUsers', {
                initialValue: subscription && subscription.maxUsers ? subscription.maxUsers : '',
                rules: [
                  {
                    required: true,
                    message: messages.subscription.rules.maxUsers.required,
                  },
                ],
              })(<InputNumber min={0} />)}
            </FormItem>
          </Col>
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.maxClones} hasFeedback>
              {getFieldDecorator('maxClones', {
                initialValue: subscription && subscription.maxClones ? subscription.maxClones : '',
                rules: [
                  {
                    required: true,
                    message: messages.subscription.rules.maxClones.required,
                  },
                ],
              })(<InputNumber min={0} />)}
            </FormItem>
          </Col>
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.canUsePGB}>
              {getFieldDecorator('canUsePGB', {})(
                <Switch
                  onChange={() => {
                    subscription.canUsePGB = !subscription.canUsePGB;
                  }}
                  checked={subscription && subscription.canUsePGB ? subscription.canUsePGB : false}
                />
              )}
            </FormItem>
          </Col>
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.canTrain}>
              {getFieldDecorator('canTrain', {})(
                <Switch
                  onChange={() => {
                    subscription.canTrain = !subscription.canTrain;
                  }}
                  checked={subscription && subscription.canTrain ? subscription.canTrain : false}
                />
              )}
            </FormItem>
          </Col>
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.canUseApi}>
              {getFieldDecorator('canUseApi', {})(
                <Switch
                  onChange={() => {
                    subscription.canUseApi = !subscription.canUseApi;
                  }}
                  checked={subscription && subscription.canUseApi ? subscription.canUseApi : false}
                />
              )}
            </FormItem>
          </Col>{' '}
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.recommended}>
              {getFieldDecorator('recommended', {})(
                <Switch
                  onChange={() => {
                    subscription.recommended = !subscription.recommended;
                  }}
                  checked={
                    subscription && subscription.recommended ? subscription.recommended : false
                  }
                />
              )}
            </FormItem>
          </Col>{' '}
          <Col xs={24} md={12}>
            <FormItem label={messages.subscription.active}>
              {getFieldDecorator('active', {})(
                <Switch
                  onChange={() => {
                    subscription.active = !subscription.active;
                  }}
                  checked={subscription && subscription.active ? subscription.active : false}
                />
              )}
            </FormItem>
          </Col>
          <Col xs={24} md={12}></Col>
        </Row>
      </Form>
    </Modal>
  );
};

const SubscriptionsForm = Form.create()(SubscriptionsFormCmp);

export { SubscriptionsForm };
